main {
    margin: 25px 0 50px 0;

    #google-map-container {
        width: 100%; 
        height: 500px;
        margin-top: 50px;
        margin-bottom: -50px;

        .mapouter {
            position: relative;
            text-align: right;
            height: 100%;
            width: 100%;
    
            .gmap_canvas {
                overflow: hidden;
                background: none !important;
                height: 100%;
                width: 100%;
    
                iframe {
                    height: 100%;
                    width: 100%;
                }
            }
        }
    }

    #address-panel {
        & > div {
            display: grid;
            grid-template-columns: auto 1fr;
            gap: 15px;

            & > .icon-container {
                height: fit-content;
                width: fit-content;
                background-color: aqua;
                padding: 15px;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }
}