header {
    width: 100%;
    height: 100vh;
    position: relative;

    #topbar {
        position: fixed;
        width: 100%;
        background-color: #000;
        color: #fff;
        padding: 15px 0;
        z-index: 2;

        #language-selector {
            position: absolute;
            right: 0;
            bottom: 0;
            transform: translateY(50%);
            background-color: #000;
            padding: 10px;
            border-bottom-left-radius: 10px;

            img {
                width: 55px;
                transition: 0.2s;
            }

            &:hover {

                img {
                    width: 55px;
                }

                & > .dropdown {
                    display: grid;
                }
            }

            .dropdown {
                display: none;
                position: absolute;
                right: 0;
                background: #000;
                padding: 20px;
                grid-template-rows: auto auto;
                gap: 20px;
                transition: 0.2s;

                img {
                    cursor: pointer;

                    &:hover {
                        transform: scale(1.1);
                    }
                }
            }
        }
    }
    
    #topslider {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 1;
    
        & > .slideImageShade {
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            background-color: rgba(0, 0, 0, 0.4);
            z-index: 2;
        }

        & > *:not(img) {
            z-index: 3;
        }

        .carousel, .carousel-inner, .carousel-item {
            height: 100%;
            width: 100%;
        }

        & > .carousel-container {
            position: relative;

            .logo {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);

                svg {
                    width: 350px;

                    & > g {
                        fill: #fff;
                    }
                }
            }

            .carousel-control-prev, .carousel-control-next {
                width: 35px !important;
            }
    
            .carousel-indicators {
                margin-bottom: 100px;
    
                & > button {
                    width: 15px;
                    height: 15px;
                    margin-right: 7px;
                    margin-left: 7px;
                    border-radius: 50%;
                }
            }

            .carousel-caption {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto !important;
                height: fit-content;
                padding: unset;
                text-align: left;

                h3 {
                    margin: 0;
                }
            }
        }
    }
}